@import url(https://fonts.googleapis.com/css?family=Dancing+Script:400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Dancing+Script:400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: black;
}
.topheader{
  background-color:black;
}

.navbar h2{
  color:white;
  font-family: 'Dancing Script', cursive;
  text-align:center;
}



.App {
  text-align: center;
}


.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.button-section {
    text-align:center;
    max-width:70%;
    
}

.button-section button {
    margin:.5em;
    background-color:#E71E62;
    border-color: #E71E62;
    color:#020001;
    display:inline-block;

}
.button-section img{
    margin-right:0.25em;
}


.button-section button:hover{
    color:#E71E62;
    background-color:#020001;
    border-color:#020001;

}

.heading-section {
    margin:.25em;
    text-align:center;
    display:inline-block;

}
.heading-section h1{
    font-family: 'Dancing Script', cursive;
    color: #FCFCFC;
}
.heading-section p{
    color:#E53770;
    letter-spacing:0.15em;
    font-size:1.25em;
}
.inline-block{
    display:inline-block;
}
.text-center{
    text-align:center;
}
 hr {
    max-width:20%;
    height:.025em;
    background-color: rgba(255, 255, 255, 0.342);
    
} 
.heading-section img{
    max-width:20%;
    max-height:20%;
    border: 1px;
    border-radius:50%;
}
.content-section {
    font-family: 'Source Sans Pro', sans-serif;
    background-color:black;
    text-align:center;
}
.footer-section {
    text-align:center;
    margin-top:2em;
    color:white;
    display: inline-block;
}
.footer-section ul {
    list-style: none;
}
.footer-section p {
    font-size:.8em;
}
.my-branding {
    color:grey;
    font-size:0.75em;
    padding-right:3em;
    margin-top:2em;
    /* text-align:center; */
}
.my-branding p{
    font-size:0.75em;
}
.contact-section{
    text-align:center;
}
.contact-section li{
    padding-right:2em;
}
.social-media-icon {
    border-radius:50%;
    max-width:20%;
    max-height:20%;
}


